export default function location({ HOST = 'appsworld.gamifive-app.com', GAME_ID = 'c2701133414427fee732e051abdfe3e8', COUNTRY_CODE = 'ww-it' }) {
  return {
    hash: '',
    search: '',
    pathname: `/${COUNTRY_CODE}/html5gameplay/${GAME_ID}/game/sample`,
    port: '',
    hostname: `${HOST}`,
    host: `${HOST}`,
    protocol: 'http:',
    origin: `http://${HOST}`,
    href: `http://${HOST}/${COUNTRY_CODE}/html5gameplay/${GAME_ID}/game/sample`,
  };
}



